@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.App {
  max-width: 860px;
  margin: 40px auto;
}

button{
  background:none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}


button:hover {
  background: #c23866;
  color:#fff;
  transition-delay: 0.25s;
}


.card-grid {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
